.info-modal-container {
    position: fixed;
    top: 55px;
    right: 5px;
    z-index: 1000;
}

.contact-info {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: inherit;
  }
  
  .contact-item {
    display: flex;
    gap: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    span {
      font-size: large;
    }
  }
  
  .contact-icon {
    width: 30px;
    height: 30px;
    vertical-align: middle;
  }

  .info-icon {
    width: 30px;
    height: 30px;
    cursor: pointer;
    background-color: white;
    border-radius: 8px;
    z-index: 1000;
  }
  
  .overlay {
    z-index: 1000;
  }