.menu {
    display: flex;
    justify-content: space-between;
    /* margin-bottom: 20px; */
    height: 50px;
  }
  
  .menu-button {
    flex: 1;
    text-align: center;
    padding: 10px;
    background-color: red;
    color: white;
    text-decoration: none;
    border: none;
    cursor: pointer;
  }
  
  .menu-button:hover {
    background-color: darkred;
  }
  