.map-container {
  position: relative;
  height: calc(100dvh - 50px);
}

.map-filter {
  position: absolute;
  bottom: 20px;
  left: 20px;
  z-index: 900;
  background: rgba(255, 255, 255, 0.8);
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  font-size: 14px;
  touch-action: manipulation;
}

.gps-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  color: black;
  text-align: center;
  padding: 20px;
  z-index: 5001;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.gps-popup button {
  margin-top: 10px;
  padding: 10px 20px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.gps-popup button:hover {
  background-color: darkred;
}

.leaflet-marker-icon {
  z-index: 1000;
}

.color-dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 5px;
}

.color-dot.red {
  background-color: red;
}

.color-dot.green {
  background-color: lightgreen;
}

.color-dot.blue {
  background-color: blue;
}

.color-dot.black {
  background-color: black;
}

p {
  margin-top: 5px;
  margin-bottom: 5px;
}

h3 {
  margin-bottom: 0;
}

.leaflet-popup-content-wrapper {
  font-size: larger;
}

.leaflet-popup-content p {
  margin: 15px 0;
}





