/* Stili generali */
.admin-container {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
  background-color: #f8f9fa;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
}

.admin-title {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
}

/* Stili per le sezioni */
.event-form,
.delete-section,
.calculation-section,
.scores-section {
  background-color: #ffffff;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.event-form h2,
.delete-section h2,
.calculation-section h2 {
  margin-bottom: 15px;
  color: #444;
  font-size: 18px;
}

/* Input e textarea */
input,
textarea {
  width: 90%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
  transition: all 0.3s ease-in-out;
}

textarea {
  resize: vertical;
  min-height: 80px;
}

/* Effetto focus sugli input */
input:focus,
textarea:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.3);
  outline: none;
}

/* Stili per i bottoni */
.button-group {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

button {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.submit-button {
  background-color: #28a745;
  color: white;
}

.update-button {
  background-color: #ffc107;
  color: black;
}

.delete-button {
  background-color: #dc3545;
  color: white;
}

.calculate-button {
  background-color: #007bff;
  color: white;
  width: 100%;
}
.refresh-button {
  background-color: pink;
  color: black;
  width: 100%;
  margin-right: 10px;
}
.update-button-score {
  background-color: palevioletred;
  color: black;
  width: 100%;
  margin-left: 10px;
}

/* Effetti Hover e Click */
button:hover {
  opacity: 0.8;
}

button:active {
  transform: scale(0.95);
}

/* Sezione Posizione */
.position-inputs {
  display: flex;
  gap: 10px;
}

.position-inputs input {
  flex: 1;
}

/* Stili per il pulsante di calcolo */
.calculation-section .calculate-button {
  width: 100%;
  font-size: 16px;
  padding: 12px;
  font-weight: bold;
}
