.login-container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: -webkit-fill-available;
  .info-modal-container {
    position: unset !important;
    width: 30px;
    height: 30px;
  }
}

.login-container h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: red;
  text-align: center;
  display: flex;
  justify-content: center;
  vertical-align: middle;
  gap: 10px;
}

.login-container h4 {
  margin-bottom: 20px;
  font-size: 14px;
  color: black;
  text-align: center;
}

.login-container input {
  width: -webkit-fill-available;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
}

.login-container button {
  width: 100%;
  padding: 10px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
  margin-bottom: 10px;
  
}

.login-container button:hover {
  background-color: darkred;
}

.participate-checkbox {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  width: 200px;
}

.participate-checkbox input {
  margin: 10px;
  flex: 0.2;
}

.participate-checkbox label {
  font-size: 16px;
  color: #333;
}

.gender-dropdown {
  width: -webkit-fill-available;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  background-color: white;
}

.password-container {
  position: relative;

  .password-button {
    width: auto;
    align-self: flex-end;
    padding: 5px;
    background-color: red;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;
    margin-bottom: 10px;
    position: absolute;
    top: 6px;
    right: 10px;
    box-sizing: border-box;
  }
}
