.regolamento {
    padding: 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 20px;
  }
  
  .regolamento h1 {
    font-size: 24px;
    color: #333;
    margin-bottom: 10px;
  }

  .regolamento h2 {
    font-size: 20px;
    color: #333;
    margin-bottom: 10px;
  }
  
  .regolamento p {
    font-size: 16px;
    color: #666;
    line-height: 1.5;
  }